import { Component } from "react";
import './APP.css';
import Selectbox from "./component/Selectbox"

class App extends Component {
  constructor(props){
    super(props);
    var ColorList =[];
    var RandomColor;
    // var keylist=[];
    for(let i=0;i<10;i++){
      RandomColor = `#${Math.round(Math.random() * 0xffffff).toString(16)}`;
      // keylist.push(i);
      ColorList.push(RandomColor);
    }
    
    this.state={
      // list:{color:ColorList, a:"keylist"}
      // color:ColorList
      ColorList
    }
  }
    
  render(){
    return (
    // <Selectbox listcolor = {this.state.ColorList}></Selectbox>
    <Selectbox ColorList = {this.state.ColorList}></Selectbox>
      );
  }
}

export default App;
