import { Component } from "react";
import './selectbox.css';

class Selectbox extends Component{
  beforeText;
  handleClick = (e) => {
    console.log(this.beforeText);
    document.querySelector("body").style.backgroundColor=
    e.nativeEvent.path[0].style.backgroundColor;

    
    e.nativeEvent.path[1].children[1].style.color = "white";

    if(this.beforeText != null){
      this.beforeText.style.color = "black";
    }
    this.beforeText = 
      e.nativeEvent.path[1].children[1];

    e.nativeEvent.path[3].children[0].innerText =
    e.nativeEvent.path[1].children[1].innerText;
  }
    render(){
      var ColorList = this.props.ColorList;
        return(
        <div className="container">
          <div className="copypaste"></div>
          <div className="listBox">
          {ColorList.map((ColorList, index) =>(
          <div className="selectlist"
            key={index}>
            <div className="color"
            onClick={(e) => this.handleClick(e)}
            style={{backgroundColor:`${ColorList}`}}>
            </div>
            <div className="hexcolor">{ColorList}</div>
            
          </div>
          ))}
          </div>
        </div>
        
        
        );
    }

}


export default Selectbox;